@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  color: #222;
  width: 100%;
  background: #EEEEEE;
  font-family: "Open Sans", sans-serif;
}
main {
  font-size: 1.2rem;
  width: 90%;
  margin: 1rem auto;
}

h3 {
  margin: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

#infos {
  display: table;
}

#infos span {
  display: block;
  font-family: monospace;
}

#infos div {
  padding: 0.6rem 1rem;
  margin: 1rem auto;
  background: #FAFAFA;
  border-radius: 2px;
  box-shadow: 1px 1px 4px #BBB;
}

.right {
  float: right;
}

.green {
  color: #060;
}

.blue {
  color: #00A;
}

.red {
  color: #A00;
}
